.profile-experience * {
    margin: 0;
    box-sizing: border-box;
  }
  
  :root {
    font-size: 13px;
    font-family: 'Source Sans Pro', sans-serif;
    line-height: 1.618;
    font-weight: 400;
  }
  
  body {
    background-color: #2f3238;
    color: #f5f5f5;
  }
  
  .profile-experience a {
    color: #1abc89;
  }
  
  .profile-experience a:hover {
    opacity: .8;
  }
  
  .profile-experience p {
    font-size: 1.2rem;
    color: rgba(245, 245, 245, 0.5);
  }
  
  .profile-experience .small {
    font-size: 1rem;
    margin-top: 1em;
  }

  .profile-experience .small h3{    
    margin-bottom: 1em;
  }

  .profile-experience .small ul{
    margin:0;
    padding:0;
  }

  .profile-experience .small ul li {
    display: inline;
    margin: 0 2px;
    background-color: #504848;
    padding: 2px 3px;
    border-radius: 10%;
  }
  
  .profile-experience {
    max-width: 1200px;
    margin: 0 auto;
    padding: 1em;
  }
  
  .profile-experience header {
    text-align: center;
    padding-bottom: 3rem;
  }
  
  .profile-experience h1 {
    font-size: 2.6rem;
    line-height: 1.2em;
    font-weight: 600;
  }
  
  .profile-experience svg {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
  }
  
  .profile-experience circle {
    -webkit-transform-origin: 50% 50%;
            transform-origin: 50% 50%;
    -webkit-transform: scale(0);
            transform: scale(0);
    transition: -webkit-transform 200ms cubic-bezier(0.25, 0.46, 0.45, 0.94);
    -webkit-transition: -webkit-transform 200ms cubic-bezier(0.25, 0.46, 0.45, 0.94);
    transition: transform 200ms cubic-bezier(0.25, 0.46, 0.45, 0.94);
    transition: transform 200ms cubic-bezier(0.25, 0.46, 0.45, 0.94), -webkit-transform 200ms cubic-bezier(0.25, 0.46, 0.45, 0.94);
  }

  
  .profile-experience circle {

    -webkit-transform: scale(1);
            transform: scale(1);

  }

  .profile-experience text {
    font-size: 1.1rem;
    text-transform: uppercase;
    text-anchor: middle;
    letter-spacing: 1px;
    font-weight: 600;
  }
  
  .profile-experience .svg-text {
    fill: #545a64;
  }
  
  .profile-experience .svg-masked-text {
    fill: white;
  }
  
  .profile-experience image {
    -webkit-transform: scale(1.1);
            transform: scale(1.1);
    -webkit-transform-origin: 50% 50%;
            transform-origin: 50% 50%;
    transition: -webkit-transform 200ms cubic-bezier(0.25, 0.46, 0.45, 0.94);
    -webkit-transition: -webkit-transform 200ms cubic-bezier(0.25, 0.46, 0.45, 0.94);
    transition: transform 200ms cubic-bezier(0.25, 0.46, 0.45, 0.94);
    transition: transform 200ms cubic-bezier(0.25, 0.46, 0.45, 0.94), -webkit-transform 200ms cubic-bezier(0.25, 0.46, 0.45, 0.94);
  }
  
  .profile-experience .items {
    display: flex;
    flex-flow: row wrap;
    justify-content: center;
  }
  
  .profile-experience .item {
    display: flex;
    align-items: center;
    justify-content: center;
    position: relative;
    overflow: hidden;
    width: 300px;
    height: 200px;
    margin: 5px;
    cursor: pointer;
    background-color: #3b3e46;
    border-radius: 2px;
    box-shadow: 0 5px 5px rgba(0, 0, 0, 0.02), inset 0 0px 0px 1px rgba(0, 0, 0, 0.07);
    -webkit-transform: translateZ(0);
            transform: translateZ(0);
  }
  .profile-experience .item rect {
    -webkit-transform: scale(1);
            transform: scale(1);  
  }  

  .profile-experience .item:hover rect {
    -webkit-transform: scale(0);
            transform: scale(0);  
  }  
  .profile-experience .item:hover circle,
  
  .profile-experience .item:hover image {
    -webkit-transform: scale(1);
            transform: scale(1);
  }
  .profile-experience .item .item-container{
    opacity: 0.3;
  }
  .profile-experience .item:hover .item-container{
    opacity: 1;
  }
  .profile-experience button {
    width: 12px;
    height: 12px;
    border: none;
    -webkit-appearance: none;
       -moz-appearance: none;
            appearance: none;
    box-shadow: 0 0 0 1px rgba(0, 0, 0, 0.5);
    border-radius: 1px;
  }
  .profile-experience button.dark {
    background-color: #2f3238;
  }
  .profile-experience button.light {
    background-color: #f9f9f9;
  }
  
  .profile-experience .options {
    position: absolute;
    top: 1rem;
    right: 1rem;
  }
  .profile-experience .options button {
    margin-left: .5rem;
  }
  
  .profile-experience .light {
    background-color: #f9f9f9;
    color: #1a1a1a;
  }
  .profile-experience .light p {
    color: rgba(26, 26, 26, 0.5);
  }
  .profile-experience .light .item {
    background: #f5f5f5;
  }
  .profile-experience .light .svg-text {
    fill: rgba(0, 0, 0, 0.1);
  }
